import React from "react";
import { NavLink } from "react-router-dom";
import "./home.css"
import web from "../src/images/home-slider1.png";
import web1 from "../src/images/home-slider2.png";
import web2 from "../src/images/home-banner-04.jpeg";
import Selectioncollege from "../src/images/college.jpg";
import Selectionexamination from "../src/images/examination.jpg";
import Selectionhelp from "../src/images/help.jpg";
import Selectiontimetable from "../src/images/timetable.jpg";
import Carousel from "react-bootstrap/Carousel";
const Home = () => {
  return (
    <>
        <div>
          <div classNameName="container-flex">
            <Carousel>
              <Carousel.Item style={{ height: "500px" }}>
                <img
                  style={{ height: "500px" }}
                  classNameName="d-block w-100"
                  src={web}
                />
                <Carousel.Caption>
                  <h3>First Demo </h3>
                </Carousel.Caption>
              </Carousel.Item>
              <Carousel.Item style={{ height: "500px" }}>
                <img
                  style={{ height: "500px" }}
                  classNameName="d-block w-100"
                  src={web1}
                />
                <Carousel.Caption>
                  <h3>Second Demo</h3>
                </Carousel.Caption>
              </Carousel.Item>
              <Carousel.Item style={{ height: "500px" }}>
                <img
                  style={{ height: "500px" }}
                  classNameName="d-block w-100"
                  src={web2}
                />
                <Carousel.Caption>
                  <h3>Third Demo</h3>
                </Carousel.Caption>
              </Carousel.Item>
            </Carousel>
          </div> 
          <Carousel>
        <img src={Selectioncollege}  style={{width: "200px", padding: "15px" , alignItems: "center"}} alt='imagem' title='imagem' />
        <img src={Selectionexamination} style={{width: "200px", padding: "15px"}} alt='imagem' title='imagem' />
        <img src={Selectiontimetable}  style={{width: "200px", padding: "15px"}} alt='imagem' title='imagem' />
        <img src={Selectionhelp} style={{width: "200px", padding: "15px"}} alt='imagem' title='imagem' />
        
    </Carousel>
          {/* section Slider */}
          <div className="new-shoes-background" id="new-shoes-area">
          <div className="new-shoes-section">
                <ul id="autoWidth" className="cs-hidden">
                    <li className="item-a">
                        <div className="box">
                            <div className="new-shoes-image-area">
                                <a href="#"><img src={Selectioncollege} alt="Morning Brew Ballet Shoe"/></a>
                            </div>
                            <div className="new-shoes-text">
                                <h3 className="new-shoes-text-title">Morning Brew Ballet</h3>
                                <h3 className="new-shoes-text-brand">By ComfiTech</h3>
                            </div>
                        </div>
                    </li>
                    <li className="item-a">
                        <div className="box">
                            <div className="new-shoes-image-area">
                                <a href="#"><img src="img/n_shoes1.jpg" alt="Old Green Slime Shoe"/></a>
                            </div>
                            <div className="new-shoes-text">
                                <h3 className="new-shoes-text-title">Old Grey Slime</h3>
                                <h3 className="new-shoes-text-brand">By ComfiTech</h3>
                                <h4>₹3499.00</h4>
                                <p className="new-shoes-text-details">Produced by New ComfiTech and named as Slimes because the colors of the shoes are inspired by monsters that often appear in fantasy stories, namely Slimes. These shoes have Premium quality and are not easily damaged.</p>
                            </div>
                        </div>
                        </li>
                        </ul>
                        </div>
                        </div>
        </div>
    </>
  );
};

export default Home;
